.header {
  background-color: #ffffffaa;
  border-bottom: thin solid lightgray;
  width: 90vw;
  padding: 1vh 0;
  position: fixed;
  left: 5vw;
  top: 0;
  z-index: 1;
  display: flex;
  align-items: center;
}

.header-title {
  position: relative;
  font-family: "Times New Roman", Times, serif;
  font-size: 5vh;
  left: 1vw;
}

.nav-button-container {
  position: absolute;
  right: 0;
}

.nav-button-container .nav-button {
  background-color: lightblue;
  border-radius: 10%;
  cursor: pointer;
  padding: 1vh 1vw;
  margin-right: 2vw;
}

.hamburger {
  position: absolute;
  right: 10vw;
  width: 5vw;
  height: 5vw;
  background-color: #ffffffaa;
  border-radius: 20%;
}

.hamburger:hover {
  background-color: lightgray;
}

.hamburger-line-top {
  background-color: black;
  height: 0.5vw;
  width: 100%;
  border-radius: 20%;
  position: absolute;
  top: 20%;
}

.hamburger-line-middle {
  background-color: black;
  height: 0.5vw;
  width: 100%;
  border-radius: 20%;
  position: absolute;
  top: 45%;
}

.hamburger-line-bottom {
  background-color: black;
  height: 0.5vw;
  width: 100%;
  border-radius: 20%;
  position: absolute;
  top: 70%;
}

.hamburger .nav-button {
  background-color: #ADD8E6aa;
  position: relative;
  border-radius: 10%;
  margin-top: 2px;
  right: 20px;
  top: 8vh;
  min-width: 40px;
}

.nav-button:hover {
  background-color: yellow;
}

.profile-pic-container {
  width: 67vw;
  margin: 12vh auto;
}

.profile-pic {
  background-image: url("https://media.licdn.com/dms/image/C5103AQHQpJT6kqTtzg/profile-displayphoto-shrink_200_200/0?e=1571875200&v=beta&t=o9x6yZB9O33CzYfhVcewmiLMKz1YM4lOlpgxwtqt3tw");
  background-size: cover;
  background-repeat: no-repeat;
  margin: auto;
  padding: 15vh 0;
  width: 30vh;
  border-radius: 50%;
  color: red;
}

.about-container {
  border-top: thin solid lightgray;
  width: 67vw;
  margin: 5vh auto;
}

.cover-paragraph {
  font-size: 12px;
  text-indent: 3vw;
}

.projects-container {
  border-top: thin solid lightgray;
  max-width: 60vw;
  margin: 5vh auto;
}

.ant-carousel .slick-slide {
  text-align: center;
  line-height: 30vw;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

.slick-dots {
  padding: 5px 0 !important;
  position: relative !important;
  background-color: #d3d3d355;
  width: 100px !important;
  margin: auto !important;
}


#catan {
  background-image: url('./project_images/Catan.png');
  background-size: cover;
  background-repeat: no-repeat;
}

#shiny {
  background-image: url('./project_images/Shiny_App.png');
  background-size: cover;
  background-repeat: no-repeat;
}

#coharie {
  background-image: url('./project_images/Coharie_Planning_project.png');
  background-size: cover;
  background-repeat: no-repeat;
}

.project-link {
  margin-right: 5px;
}

.contact-container {
  border-top: thin solid lightgray;
  width: 67vw;
  margin: 5vh auto;
}

.email-text-entry {
  height: 60vh;
  width: 67vw;
  overflow: hidden;
}
